
import Link from 'next/link';
import { notFound } from 'next/navigation';
import { getCompetitionWithWorks, getUserSession } from '@/app/actions';
import type { HistoricalCompetitionWithWorks } from '@/app/actions';
import type { User } from '@/types';
import BuildingCard from '@/components/minecraft/BuildingCard';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { ArrowLeft, CalendarDays, Info, Trophy, Users } from 'lucide-react';
import { format, parseISO } from 'date-fns';
import { cn } from '@/lib/utils';

export const dynamic = 'force-dynamic';

interface CompetitionDetailPageProps {
  params: { id: string };
}

export async function generateMetadata({ params }: CompetitionDetailPageProps) {
  const competitionData = await getCompetitionWithWorks(params.id);
  if (!competitionData) {
    return {
      title: '活动未找到',
    };
  }
  return {
    title: `${competitionData.name} - 历史活动回顾`,
    description: `查看 ${competitionData.year}年 ${competitionData.name} 活动的详细信息和参赛作品。${competitionData.description || ''}`,
  };
}

const getStatusBadgeClass = (status: Competition['status']) => {
  switch (status) {
    case '进行中': return 'bg-blue-500/20 text-blue-400 border-blue-500/30';
    case '策划中': return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
    case '评审中': return 'bg-purple-500/20 text-purple-400 border-purple-500/30';
    case '已结束': return 'bg-green-500/20 text-green-400 border-green-500/30';
    case '已归档': return 'bg-gray-500/20 text-gray-400 border-gray-500/30';
    default: return 'bg-muted text-muted-foreground';
  }
};

export default async function CompetitionDetailPage({ params }: CompetitionDetailPageProps) {
  const currentUser: User | null = await getUserSession();
  const competitionData: HistoricalCompetitionWithWorks | undefined = await getCompetitionWithWorks(params.id, currentUser);

  if (!competitionData) {
    notFound();
  }

  const { name, year, status, startDate, endDate, description, works } = competitionData;

  return (
    <div className="space-y-8">
      <Link href="/history" className="inline-flex items-center text-sm text-primary hover:underline mb-2">
        <ArrowLeft className="mr-1 h-4 w-4" />
        返回活动列表
      </Link>

      <Card className="shadow-xl overflow-hidden">
        <CardHeader className="bg-card/80 p-6 border-b border-border">
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-3">
            <div className="flex items-center gap-3">
                <Trophy className="h-8 w-8 text-primary shrink-0" />
                <CardTitle className="text-3xl font-bold text-primary">{name}</CardTitle>
            </div>
            <Badge className={cn("text-sm px-3 py-1.5", getStatusBadgeClass(status))}>{status}</Badge>
          </div>
          <div className="mt-2 text-sm text-muted-foreground flex flex-wrap items-center gap-x-4 gap-y-1">
            <div className="flex items-center">
              <CalendarDays className="mr-1.5 h-4 w-4" />
              <span>{year}年</span>
            </div>
            {startDate && endDate && (
              <div className="flex items-center">
                <span className="hidden sm:inline-block mx-1">|</span>
                <span>活动日期: {format(parseISO(startDate), 'yyyy/MM/dd')} - {format(parseISO(endDate), 'yyyy/MM/dd')}</span>
              </div>
            )}
          </div>
        </CardHeader>
        {description && (
          <CardContent className="p-6 text-base text-foreground/90 leading-relaxed">
            <p>{description}</p>
          </CardContent>
        )}
      </Card>

      <Separator />

      <div>
        <h2 className="text-2xl font-semibold text-foreground mb-1 flex items-center">
          <Users className="mr-2 h-6 w-6 text-primary/80" />
          参赛作品 ({works.length}个)
        </h2>
        <p className="text-muted-foreground mb-6">以下是本次活动中提交的所有作品。</p>
        {works.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {works.map((building) => (
              <BuildingCard key={building.id} building={building} currentUser={currentUser} />
            ))}
          </div>
        ) : (
          <Card className="mt-6 text-center py-10 border-dashed border-border bg-card/50">
            <CardContent className="flex flex-col items-center gap-3">
                <Info className="h-12 w-12 text-muted-foreground/70" />
                <p className="text-lg text-muted-foreground">暂无作品</p>
                <p className="text-sm text-muted-foreground/80">本次活动还没有任何作品提交或被记录。</p>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  );
}

    